$(document).ready(function() {
    $('.main-slider').slick({
        prevArrow: $('.main-slider_arrow.left'),
        nextArrow: $('.main-slider_arrow.right'),
        appendDots: $('.main-slider_dots'),
        dots: true,
    })


    var sliders = $('.slider');
    
    sliders.each(function (i, el) {
    var leftArr = ('.slider-arrow-left' + i)
    var rightArr = ('.slider-arrow-right' + i)
    $(el).slick({
        dots: false,
        slidesToShow: 4,
        slidesToScroll: 1,
        arrows: true,
        margin: 20,
        prevArrow: $(leftArr),
        nextArrow: $(rightArr),
        responsive: [
          {
            breakpoint: 1360,
            settings: {
              slidesToShow: 3,
            },
          },
          {
            breakpoint: 1198,
            settings: {
              slidesToShow: 4,
            },
          },
          {
            breakpoint: 1023,
            settings: {
              slidesToShow: 3,
            },
          },
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 2,
            },
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
            },
          },
        ]
    })
    })


    $('input[type="tel"]').mask("+7 (999) 999-99-99");

    $('.header-mob_menu-icon').click(function(){
      $('.header-mob_menu-icon .center').toggleClass('active')
      $('.aside').toggleClass('active')
      $('body').toggleClass('overflow')
    })
    if ($(window).width() < 769) {

      $('.footer_top_item_title').click(function(){
        $(this).closest('.footer_top_item').toggleClass('active')
      })
    }

    if ($(window).width() < 601) {

      $('.main-inst_content').slick({
        arrows: false,
        dots: false,
        slidesToShow: 2

      })
    }
})